
import { patch } from './vdom/patch';
export function lifecycleMixin(Vue) {
    Vue.prototype._update = function (vnode) {
        console.log('vnode', vnode)
        // Âç≥ÊúâÂàùÂßãÂåñÔºåÂèàÊúâÊõ¥Êñ∞
        // ÊØîËæÉÂâçÂêéÁöÑËôöÊãüËäÇÁÇπÁöÑÂ∑ÆÂºÇ
        const vm = this
        patch(vm.$el, vnode)
    }
}

export function mountComponent(vm, el) {
    // Êï∞ÊçÆÂèòÂåñÂêéÔºå‰ºöÂÜçÊ¨°Ë∞ÉÁî®Êõ¥Êñ∞ÂáΩÊï∞
    let updateComponent = () => {
        // 1. ÈÄöËøárenderÁîüÊàêËôöÊãüdom
        vm._update(vm._render()) // ÂêéÁª≠Êõ¥Êñ∞ÂèØ‰ª•Ë∞ÉÂä®updateComponentÊñπÊ≥ï
        // 2. ËôöÊãüDomÁîüÊàêÁúüÂÆûDom
    }
    updateComponent()
}